import React from "react"
import styles from './Profile.module.css'
import {useHistory} from "react-router-dom"
import ProfileGeneral from "./General/ProfileGeneral"
import {ProfileInfo} from "./Information/ProfileInfo"
import {useDispatch, useSelector} from "react-redux"
import {getUserProfile, savePhoto, saveProfile, updateStatus} from "../../redux/reducers/profile";
import {AppStateType} from "../../redux/store";
import {ProfileType} from "../../types";
import preloader from "../../assets/img/preloader.svg";
import {Typography} from "@mui/material";

const Profile: React.FC = React.memo(() => {

  const dispatch = useDispatch();
  const history = useHistory();
  // @ts-ignore
  const profileData: ProfileType = useSelector((state: AppStateType) => state.profile.data);

  const getUrlId = () => {
    const urlString = history.location.pathname;
    return parseInt(urlString.replace(/\D+/g, ""));
  };

  const ownerId = useSelector((state: AppStateType ) => state.auth.id);
  const urlId = getUrlId();
  const isOwner = ownerId === urlId;

  React.useEffect(() => {
    if (isOwner) {
      dispatch(getUserProfile(ownerId));
    } else {
      dispatch(getUserProfile(urlId));
    }
  }, [dispatch]);

  let [editMode, setEditMode] = React.useState<boolean>(false)
  const onsubmit = async (data: any) => {
    await dispatch(saveProfile(data));
    setEditMode(false)
    dispatch(updateStatus(data.status))
  }
  const isEmptyObject = (obj: any) => {
    for (let i in obj) {
      if (obj.hasOwnProperty(i)) {
        return false;
      }
    }
    return true;
  }

  if (!profileData) return <img style={{textAlign: 'center'}} src={preloader} alt=""/>
  return (
    <div className={styles.main}>
      <Typography>{profileData.fullName}</Typography>
      <Typography>{profileData.userId}</Typography>
      <img src={profileData.photos.large} alt=""/>
      {/*<ProfileGeneral profile={profileData} isOwner={isOwner}*/}
      {/*   // @ts-ignore*/}
      {/*  savePhoto={savePhoto} logout={null} setEditMode={setEditMode}*/}
      {/*  status={'Заглушка'}/>*/}
      {/*<ProfileInfo profile={profileData} title={'Title'}/>*/}
    </div>
  )
});

export default Profile;